import React from "react";
import styles from "./MainNav.module.scss";

type DropdownLink = {
  label: string;
  path: string;
};

type Dropdown = {
  title: string;
  hasDescription?: boolean;
  description?: string;
  links: DropdownLink[];
};

type Link = {
  label: string;
  path: string;
  hasDropdown?: boolean;
  dropdown?: Dropdown;
};

type MainNavProps = {
  links: Link[];
  onNavigate: (path: string) => void;
  activePath: string;
};

const MainNav = ({ links, onNavigate, activePath }: MainNavProps) => {
  const handleNavigate = (path: string) => {
    onNavigate(path);
  };

  return (
    <div className={styles.nav}>
      <ul>
        {links.map((link, index) => (
          <li key={index}>
            <div className={styles.link}>
              <a
                className={`${link.path === activePath ? styles.active : ""}`}
                onClick={() => handleNavigate(link.path)}
              >
                <span>{link.label}</span>
              </a>
            </div>

            {link.hasDropdown && link.dropdown && (
              <div className={styles.megaNav}>
                <div className={styles.subNav}>
                  <div className={styles.col}>
                    <div className={styles.text}>
                      <h3>{link.dropdown.title}</h3>
                      {link.dropdown.hasDescription && (
                        <p>{link.dropdown.description}</p>
                      )}
                    </div>

                    <div className={styles.subNavLinks}>
                      {link.dropdown.links.map((link) => (
                        <a href="#"></a>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default MainNav;
